import React, { Suspense, lazy } from "react"
import { Redirect, Router, Switch, Route } from "react-router-dom"
import { history } from "./history"
import { connect } from "react-redux"
import Spinner from "./components/@vuexy/spinner/Loading-spinner"
import { ContextLayout } from "./utility/context/Layout"

// Route-based code splitting
const Register = lazy(() => import("./views/pages/authentication/Register"))
const Login = lazy(() => import("./views/pages/authentication/Login"))
const LockScreen = lazy(() => import("./views/pages/authentication/LockScreen"))
const Logout = lazy(() => import("./views/pages/authentication/Logout"))
const ResetPassword = lazy(() => import("./views/pages/authentication/ResetPassword"))
const ForgotPassword = lazy(() => import("./views/pages/authentication/ForgotPassword"))

const comingSoon = lazy(() => import("./views/pages/misc/ComingSoon"))
const error404 = lazy(() => import("./views/pages/misc/error/404"))
const error500 = lazy(() => import("./views/pages/misc/error/500"))
const authorized = lazy(() => import("./views/pages/misc/NotAuthorized"))
const maintenance = lazy(() => import("./views/pages/misc/Maintenance"))

const Dashboard = lazy(() => import("./views/pages/Dashboard"))

//SECURITY
const Roles = lazy(() => import('./views/pages/Security/Roles/Roles'))
const Groups = lazy(() => import('./views/pages/Security/Groups/Groups'))
const Users = lazy(() => import('./views/pages/Security/Users/Users'))

//SETUPS
const EconomicZone = lazy(() => import('./views/pages/Setups/EconomicZone/EconomicZone'))
const PermitType = lazy(() => import('./views/pages/Setups/PermitType/PermitType'))
const GeneralSettings = lazy(() => import('./views/pages/Setups/GeneralSettings/GeneralSettings'))
const SystemStatus = lazy(() => import('./views/pages/Setups/SystemStatus/SystemStatus'))
const AdministrativeStructures = lazy(() => import('./views/pages/Setups/AdministrativeStructures/AdministrativeStructures'))
const AdministrativeTypes = lazy(() => import('./views/pages/Setups/AdministrativeTypes/AdministrativeTypes'))
const ClientType = lazy(() => import('./views/pages/Setups/ClientType/ClientType'))
const ClientDocumentTypes = lazy(() => import('./views/pages/Setups/ClientDocumentTypes/ClientDocumentTypes'))
const BusinessTypes = lazy(() => import('./views/pages/Setups/BusinessTypes/BusinessTypes'))
const BusinessDocumentTypes = lazy(() => import('./views/pages/Setups/BusinessDocumentTypes/BusinessDocumentTypes'))
const FiscalYear = lazy(() => import('./views/pages/Setups/FiscalYear/FiscalYear'))
const FiscalYearPeriods = lazy(() => import('./views/pages/Setups/FiscalYearPeriods/FiscalYearPeriods'))
const CategoryTypes = lazy(() => import('./views/pages/Setups/CategoryTypes/CategoryTypes'))
const DocumentTypes = lazy(() => import('./views/pages/Setups/DocumentTypes/DocumentTypes'))
const WorkflowMaster = lazy(() => import('./views/pages/Setups/WorkflowMaster/WorkflowMaster'))
const PaymentMode = lazy(() => import('./views/pages/Setups/PaymentMode/PaymentMode'))
const BankAccount = lazy(() => import('./views/pages/Setups/BankAccounts/BankAccount'))
const SubCounties = lazy(() => import('./views/pages/Setups/SubCounties/SubCounties'))
const Wards = lazy(() => import('./views/pages/Setups/Wards/Wards'))
const Department = lazy(() => import('./views/pages/Setups/Department/Department'))
const Modules = lazy(() => import('./views/pages/Setups/Modules/Modules'))
const ChartOfAccounts = lazy(() => import('./views/pages/Setups/ChartOfAccounts/ChartOfAccounts'))
const BillingFrequency = lazy(() => import('./views/pages/Setups/BillingFrequency/BillingFrequency'))
const FeeCategory = lazy(() => import('./views/pages/Setups/FeeCategory/FeeCategory'))
const Fees = lazy(() => import('./views/pages/Setups/Fees/Fees'))
const Estates = lazy(() => import('./views/pages/Setups/Estates/Estates'))
const MarketTypes = lazy(() => import('./views/pages/Setups/MarketTypes/MarketTypes'))
const MarketNames = lazy(() => import('./views/pages/Setups/MarketNames/MarketNames'))
const HouseTypes = lazy(() => import('./views/pages/Setups/HouseTypes/HouseTypes'))
const DocumentNumber = lazy(() => import('./views/pages/Setups/DocumentNumber/DocumentNumber'))
const Categories = lazy(() => import('./views/pages/Setups/Categories/Categories'))
const COACategory = lazy(() => import('./views/pages/Setups/COACategory/COACategory'))
const COASubCategory = lazy(() => import('./views/pages/Setups/COASubCategory/COASubCategory'))
const BettingTypes = lazy(() => import('./views/pages/Setups/BettingTypes/BettingTypes'))
const HouseDetails = lazy(() => import('./views/pages/Setups/HouseDetails/HouseDetails'))
const WeightDevices = lazy(() => import('./views/pages/Setups/WeightDevices/WeightDevices'))



//Registers
const BusinessRegistration = lazy(() => import('./views/pages/Registration/BusinessRegistration/BusinessRegistration'))
const ClientRegistration = lazy(() => import('./views/pages/Registration/ClientRegistration/ClientRegistration'))
const HouseRegistration = lazy(() => import('./views/pages/Registration/HouseRegistration/HouseRegistration'))
const BusinessInspection = lazy(() => import('./views/pages/Registration/BusinessInspection/BusinessInspection'))
const MarketRegistration = lazy(() => import('./views/pages/Registration/MarketsRegistration/MarketsRegistration'))
const LiquorRegistration = lazy(() => import('./views/pages/Registration/LiquorRegistration/LiquorRegistration')) 

const WeightsRegistration = lazy(() => import('./views/pages/Registration/WeightsRegistration/WeightsRegistration'))
const BettingRegistration=lazy(() => import('./views/pages/Registration/BettingRegistration/BettingRegistration'))


// Set Layout and Component Using App Route
const RouteConfig = ({
  component: Component,
  fullLayout,
  permission,
  user,
  ...rest
}) => (
    <Route
      {...rest}
      render={props => {
        return (
          <ContextLayout.Consumer>
            {context => {
              let LayoutTag =
                fullLayout === true
                  ? context.fullLayout
                  : context.state.activeLayout === "horizontal"
                    ? context.horizontalLayout
                    : context.VerticalLayout
              return (
                <LayoutTag {...props} permission={props.user}>
                  <Suspense fallback={<Spinner />}>
                    <Component {...props} />
                  </Suspense>
                </LayoutTag>
              )
            }}
          </ContextLayout.Consumer>
        )
      }}
    />
  )
const mapStateToProps = state => {
  return {
    user: state.auth.user
  }
}

const AppRoute = connect(mapStateToProps)(RouteConfig)

class AppRouter extends React.Component {
  render() {

    const isAuthorized = this.props.user !== undefined

    return (
      // Set the directory path if you are deploying in sub-folder
      <Router history={history}>
        <Switch>

          <AppRoute path="/auth/login" component={Login} fullLayout />
          <AppRoute path="/auth/logout" component={Logout} fullLayout />
          <AppRoute path="/auth/register" component={Register} fullLayout />
          <AppRoute path="/auth/lock-screen" component={LockScreen} fullLayout />
          <AppRoute path="/auth/forgot-password" component={ForgotPassword} fullLayout />
          <AppRoute path="/auth/reset-password" component={ResetPassword} fullLayout />

          <AppRoute path="/misc/coming-soon" component={comingSoon} fullLayout />
          <AppRoute path="/misc/error/404" component={error404} fullLayout />
          <AppRoute path="/misc/error/500" component={error500} fullLayout />
          <AppRoute path="/misc/not-authorized" component={authorized} fullLayout />
          <AppRoute path="/misc/maintenance" component={maintenance} fullLayout />

          {!isAuthorized ? (
            <Redirect to="/auth/login" />
          ) : (

              <>
                <AppRoute exact path="/" component={Dashboard} />
                <AppRoute exact path="/security/roles" component={Roles} />
                <AppRoute exact path="/security/groups" component={Groups} />
                <AppRoute exact path="/security/users" component={Users} />



                <AppRoute exact path="/registers/client-registration" component={ClientRegistration} />
                <AppRoute exact path="/registers/single-business-permit" component={BusinessRegistration} />
                <AppRoute exact path="/registers/house-registration" component={HouseRegistration} />
                <AppRoute exact path="/registers/business-inspection" component={BusinessInspection} />
                <AppRoute exact path="/registers/weight-measures" component={WeightsRegistration} />
                <AppRoute exact path="/registers/betting-registration" component={BettingRegistration} />

                <AppRoute exact path="/registers/market-registration" component={MarketRegistration} />
                <AppRoute exact path="/registers/liquor-licenses" component={LiquorRegistration} />
                <AppRoute exact path="/setups/economic-zone" component={EconomicZone} />
                <AppRoute exact path="/setups/system-status" component={SystemStatus} />
                <AppRoute exact path="/setups/permit-type" component={PermitType} />
                <AppRoute exact path="/setups/general-settings" component={GeneralSettings} />
                <AppRoute exact path="/setups/admin-structure" component={AdministrativeStructures} />
                <AppRoute exact path="/setups/department" component={Department} />
                <AppRoute exact path="/setups/modules" component={Modules} />
                <AppRoute exact path="/setups/chart-of-accounts" component={ChartOfAccounts} />
                <AppRoute exact path="/setups/fee-categories" component={FeeCategory} />
                <AppRoute exact path="/setups/coa-category" component={COACategory} />
                <AppRoute exact path="/setups/coa-sub-category" component={COASubCategory} />
                <AppRoute exact path="/setups/fees" component={Fees} />
                <AppRoute exact path="/setups/document-number" component={DocumentNumber} />
                <AppRoute exact path="/setups/categories" component={Categories} />
                <AppRoute exact path="/setups/billing-frequency" component={BillingFrequency} />
                <AppRoute exact path="/setups/admin-type" component={AdministrativeTypes} />
                <AppRoute exact path="/setups/client-type" component={ClientType} />
                <AppRoute exact path="/setups/mode-of-payment" component={PaymentMode} />
                <AppRoute exact path="/setups/work-flow-master" component={WorkflowMaster} />
                <AppRoute exact path="/setups/fiscal-year" component={FiscalYear} />
                <AppRoute exact path="/setups/fiscal-year-periods" component={FiscalYearPeriods} />
                <AppRoute exact path="/setups/category-type" component={CategoryTypes} />
                <AppRoute exact path="/setups/document-type" component={DocumentTypes} />
                <AppRoute exact path="/setups/bank-account" component={BankAccount} />
                <AppRoute exact path="/setups/sub-counties" component={SubCounties} />
                <AppRoute exact path="/setups/wards" component={Wards} />
                <AppRoute exact path="/setups/business-types" component={BusinessTypes} />
                <AppRoute exact path="/setups/client-document-types" component={ClientDocumentTypes} />
                <AppRoute exact path="/setups/business-document-types" component={BusinessDocumentTypes} />
                <AppRoute exact path="/setups/estates" component={Estates} />
                <AppRoute exact path="/setups/market-types" component={MarketTypes} />
                <AppRoute exact path="/setups/market-names" component={MarketNames} />
                <AppRoute exact path="/setups/house-types" component={HouseTypes} />
                <AppRoute exact path="/setups/betting-types" component={BettingTypes} />
                <AppRoute exact path="/setups/house-details" component={HouseDetails} />
                <AppRoute exact path="/setups/weight-devices" component={WeightDevices} />

              </>
            )}

        </Switch>
      </Router>
    )
  }
}

export default connect(mapStateToProps)(AppRouter)
